import { Box, Button, Input, MenuItem, Select, TextField } from "@mui/material"
import { useForm, Controller, SubmitHandler } from "react-hook-form"

import { yupResolver } from "@hookform/resolvers/yup"
import * as Yup from "yup"
import { Form } from "@remix-run/react"
import { FC } from "react"

interface IFormInput {
  wifeFirstName: string
  wifeLastName: string
  wifePhoneNumber: number
  wifeEmail: string
  wifeBirthYear: number
  husbandFirstName: string
  husbandLastName: string
  husbandPhoneNumber: string
  husbandEmail: string
  husbandBirthYear: number
  weddingYear: number
  city: string
  invitedBy: string
}
const schema = Yup.object().shape({
  wifeFirstName: Yup.string().required("Fullname is required"),
  wifeLastName: Yup.string().required("Fullname is required"),
  wifePhoneNumber: Yup.number().required("Fullname is required"),
  wifeEmail: Yup.string()
    .required("Email is required")
    .email("Email is invalid"),
  wifeBirthYear: Yup.number().required("Fullname is required"),
  husbandFirstName: Yup.string().required("Fullname is required"),
  husbandLastName: Yup.string().required("Fullname is required"),
  husbandPhoneNumber: Yup.number().required("Fullname is required"),
  husbandEmail: Yup.string()
    .required("Email is required")
    .email("Email is invalid"),
  husbandBirthYear: Yup.number().required("Fullname is required"),
  weddingYear: Yup.number().required("Fullname is required"),
  city: Yup.string().required("Fullname is required"),
  invitedBy: Yup.string().required("Fullname is required"),
  // username: Yup.string()
  //   .required("Username is required")
  //   .min(6, "Username must be at least 6 characters")
  //   .max(20, "Username must not exceed 20 characters"),
  // email: Yup.string().required("Email is required").email("Email is invalid"),
})

const Home: FC = () => {
  const {
    register,
    control,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm<IFormInput>({
    resolver: yupResolver(schema),
  })

  const onSubmit: SubmitHandler<IFormInput> = (data) =>
    console.log("data submitted: ", data)

  console.log(watch("husbandEmail"))
  console.log("errors are", errors)

  return (
    <div>
      {" "}
      <Form onSubmit={handleSubmit(onSubmit)}>
        <Box
          sx={{
            "& > :not(style)": { m: 1, width: "25ch" },
          }}
        >
          <h1>Żona</h1>
          <Controller
            name="wifeFirstName"
            control={control}
            defaultValue=""
            render={({ field }) => <TextField label="Imię" {...field} />}
          />
          <Controller
            name="wifeLastName"
            control={control}
            defaultValue=""
            render={({ field }) => <TextField label="Nazwisko" {...field} />}
          />
          <Controller
            name="wifePhoneNumber"
            control={control}
            render={({ field }) => (
              <TextField label="Numer telefonu" {...field} />
            )}
          />
          <Controller
            name="wifeEmail"
            control={control}
            rules={{ required: true, pattern: /^\S+@\S+$/i }}
            defaultValue=""
            render={({ field }) => <TextField label="Email" {...field} />}
          />
          <Controller
            name="wifeBirthYear"
            control={control}
            render={({ field }) => (
              <TextField label="Rok urodzenia" {...field} />
            )}
          />
          <h1>Mąż</h1>
          <Controller
            name="husbandFirstName"
            control={control}
            defaultValue=""
            render={({ field }) => <TextField label="Imię" {...field} />}
          />
          <Controller
            name="husbandLastName"
            control={control}
            defaultValue=""
            render={({ field }) => <TextField label="Nazwisko" {...field} />}
          />
          <Controller
            name="husbandPhoneNumber"
            control={control}
            defaultValue=""
            render={({ field }) => (
              <TextField label="Numer telefonu" {...field} />
            )}
          />
          <Controller
            name="husbandEmail"
            control={control}
            rules={{ required: true }}
            defaultValue=""
            render={({ field }) => (
              <TextField
                {...field}
                label="Email"
                variant="outlined"
                error={!!errors.husbandEmail}
                helperText={
                  errors.husbandEmail ? errors.husbandEmail?.message : ""
                }
                fullWidth
                margin="dense"
              />
            )}
          />
          <Controller
            name="husbandBirthYear"
            control={control}
            render={({ field }) => (
              <TextField label="Rok urodzenia" {...field} />
            )}
          />

          <h1>Wspólne</h1>
          <Controller
            name="city"
            control={control}
            defaultValue=""
            render={({ field }) => (
              <Select label="Oddział" {...field} name="city">
                <MenuItem value={1}>Wrocław</MenuItem>
                <MenuItem value={2}>Warszawa</MenuItem>
                <MenuItem value={3}>Białystok</MenuItem>
              </Select>
            )}
          />
          <Controller
            name="weddingYear"
            control={control}
            render={({ field }) => (
              <TextField type="number" label="Rok urodzenia" {...field} />
            )}
          />
          <p></p>
          <Button size="large" variant="outlined" type="submit">
            Submit
          </Button>
        </Box>
      </Form>
      {""}
    </div>
  )
}

export default Home
